import React, { useState, useContext } from 'react'
import './StageE.scss'
import Bug from '../../components/Bug/Bug'
import WhiteBlock from '../../components/WhiteBlock/WhiteBlock'
import Button from '../../components/Button/Button'
import stageContext from '../../context/stageContext'

const stageArr = ['E1', 'E2', 'E3']

const StageE = () => {
  const { userName, setLayoutColor, setStage } = useContext(stageContext)
  const [nextStage, setNextStage] = useState(0)
  const [correct, setCorrect] = useState(false)
  const [answer, setAnswer] = useState(['',''])

  const E1 = () => {
    return (
      <div className='E1'>
        <p>{userName}</p>
        <p> B  Retro</p>
        <p> Retro</p>
      </div>
    )
  }
  const E2 = () => {
    return (
      <div className='E2'>
        <p></p>
        <p></p>
        <p className='mt'> Confluence </p>
      </div>
    )
  }

  const handleInputChange = (idx, value) => {
    const newAns = answer
    newAns[idx] = value
    setAnswer([...newAns])
  }

  const handleCheckAnswer = () => {
    const newAns = answer
    if (newAns.join('') === '21') {
      setCorrect(false)
      setStage('F')
      setLayoutColor('F1')
    } else {
      setCorrect(true)
    }
  }

  const findStage = () => {
    switch (stageArr[nextStage]) {
      case 'E1':
        return <E1 />
      case 'E2':
        return <E2 />
      default:
        break;
    }
  }
  
  const E3 = () => {
    setLayoutColor('E3')
    return (
      <div className='E3'>
        <div className="head">
          <p className='title'></p>
          <p className='title'> Retro </p>
        </div>
        <div className="question">
          <div className="Q1">
            <p className='title'></p>
            <div className='inputWrap'>
              <input checked={answer[0] === '1'} onChange={e => handleInputChange(0, e.target.value)} type="radio" id="input1" name="good" value='1' />
              <label htmlFor="input1">
                <div className="check">
                  <div></div>
                </div>
                 ^_^  
              </label>
            </div>
            <div className='inputWrap'>
              <input checked={answer[0] === '2'} onChange={e => handleInputChange(0, e.target.value)} type="radio" id="input2" name="good" value='2' />
              <label htmlFor="input2">
                <div className="check">
                  <div></div>
                </div>
                 cover
              </label>
            </div>
          </div>
          <div className="Q2">
            <p className='title'></p>
            <div className='inputWrap'>
              <input checked={answer[1] === '1'} onChange={e => handleInputChange(1, e.target.value)} type="radio" id="input3" name="moreGood" value='1' />
              <label htmlFor="input3">
                <div className="check">
                  <div></div>
                </div>
                
              </label>
            </div>
            <div className='inputWrap'>
              <input checked={answer[1] === '2'} onChange={e => handleInputChange(1, e.target.value)} type="radio" id="input4" name="moreGood" value='2' />
              <label htmlFor="input4">
                <div className="check">
                  <div></div>
                </div>
                 delay 
              </label>
            </div>
          </div>
        </div>
      </div>
    )
  }

  return (
    <div className="StageE">
      <Bug
        position={'rightTop'}
        color={'#89c2ce'}
        eyeColor={'#0d0d0d'}
      />
      {stageArr[nextStage] === 'E3' ? (
        <>
          <WhiteBlock>
            <E3 />
          </WhiteBlock>
          <div className="buttonWrap">
            {correct && <span className='reTry'></span>}
            <Button onClick={() => handleCheckAnswer()} color='#fff' fontColor='#4DA3B5' text=' Retro ' />
          </div>
        </>
      ) : (
        <WhiteBlock
          button={
            <Button
              onClick={() => nextStage < 3 && setNextStage(prev => prev + 1)}
              color='#4DA3B5'
              arrow={nextStage !== 1}
              text=''
            />
          }
        >
          {findStage()}
        </WhiteBlock>
      )}
    </div>
  )
}

export default StageE