import React, { useContext, useState, useEffect } from 'react'
import './StageB.scss'
import stageContext from '../../context/stageContext'
import BSubStageLayout from './SubStage/BSubStageLayout'
import BFour from './SubStage/BFour'



const StageB = () => {
  const { userName, setLayoutColor } = useContext(stageContext)
  const [subStage, setSubStage] = useState("BOne")
  useEffect(() => {
    setLayoutColor('B1')
  })


  const BOne = () => {
    return <BSubStageLayout look handleOnClick={() => setSubStage("BTwo")} arrow={true}>
      <p>Hi{userName}</p>
      <p> TT  A  PO</p>
    </BSubStageLayout>
  }
  const BTwo = () => {
    return <BSubStageLayout smile handleOnClick={() => setSubStage("BThree")} arrow={true}>
      <p>{userName}</p>
      <p>PO  <span style={{ color: "#E14040" }}> Product owner</span></p>
      <p></p>
      <p></p>
      <p> <span style={{ color: "#3492D6" }}> Product Backlog</span> </p>
    </BSubStageLayout>
  }
  const BThree = () => {
    return <BSubStageLayout handleOnClick={() => setSubStage("BFour")} arrow={false} text={""}  >
      <p> <span style={{ color: "#999999" }}></span> </p>
      <p> <span style={{ color: "#A87D0F" }}></span></p>
      <p>{userName}</p>
      <p> <span style={{ color: "#3492D6" }}></span> </p>
    </BSubStageLayout>
  }


  return (
    <div className="StageB">
      {subStage === "BOne" && BOne()}
      {subStage === "BTwo" && BTwo()}
      {subStage === "BThree" && BThree()}
      {subStage === "BFour" && <BFour />}
    </div>
  )
}

export default StageB