import React, { useContext, useState } from 'react'
import stageContext from '../../context/stageContext'
import WhiteBlock from '../../components/WhiteBlock/WhiteBlock'
import Button from '../../components/Button/Button'
import './StageA.scss'
import Input from '../../components/Input/Input'

const StageA = () => {
  const { userName, setUserName, setStage } = useContext(stageContext)
  const [subStage, setSubStage] = useState("AOne")

  return (
    <div className="StageA">
      {subStage === "AOne" ? (<WhiteBlock button={<Button text={""} color={'#E14040'} onClick={() => setSubStage("ATwo")} />}>
        <p> TT </p>
        <p></p>
        <p> Scrum </p>
        <p className='marginTop'></p>
        <p> Scrum </p>
      </WhiteBlock>) :
        (<WhiteBlock button={<Button text={""} color={'#E14040'} onClick={() => setStage("B")} />}>
          <p className='marginBottom'></p>
          <Input value={userName} placeholder='' onChange={(e) => setUserName(e.target.value)} />
        </WhiteBlock>)}
    </div>

  )
}

export default StageA